@import "mixins";
@import "varialbles";

body{
  * {
    box-sizing:border-box;
  }
}

/* 여백 초기화 */
body,div,ul,li,dl,dd,dt,ol,h1,h2,h3,h4,h5,h6,input,fieldset,legend,p,select,
table,th,td,tr,textarea,button,form,figure,figcaption{margin:0;padding:0;}

/* a 링크 초기화 */
a {text-decoration: none;}
a:hover {text-decoration: none;}

/* 폰트 초기화 */
body, input, textarea, select, button, table 
{
  font-family: 'NanumBarunGothic', sans-serif;
  font-weight: 400;
  line-height: 1.4;
}


em, address, i{font-style: normal;}

ul,li,ol, dl, dt, dd {list-style: none;}

/* 제목 태그 초기화 */
h1,h2,h3,h4,h5,h6 {font-weight: normal;}

img {width: 100%; vertical-align: top;}
img {border:0;}  //ie10

.hidden{
  font-size: 0;
  width: 0;
  height: 0;
  color: transparent;
  opacity: 0;
  line-height: 0;
  text-indent: -9999em;
  display: block;
}

legend {
  color: inherit;
  white-space: normal;

  display: block;
  border: 0;
  max-width: 100%;
  width: 100%;
}

fieldset {
  min-width: 0;
}

[hidden] {
  display: none;
}

button{
  background: none;
  border: 0 none;
  display: inline-block;
  cursor: pointer;
  vertical-align: top;
}
textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}
@each $theme in $theme{
  @if $theme == base{
    html{
      @include theme($theme);
    }
  } @else {
    html.#{$theme}{
      @include theme($theme);
    }  
  }
} 

