@font-face {
  font-family: 'NanumBarunGothic';
  font-weight: 400;
  src: local("나눔바른고딕"),
       url(NanumBarunGothicSubset.woff2) format('woff2'),
       url(NanumBarunGothicSubset.woff) format('woff'),
       url(NanumBarunGothicSubset.ttf) format('truetype');
}
